body {
    margin: 0;
    padding: 0;
    background-color: beige;
    color: #444;
    font-family: sans-serif;
    font-size: 1.2em;
    line-height: 2em;
}

h3 {
    margin: 0;
    text-align: center;
    color: #0064ad;
}

.current-month {
    text-decoration: underline;
}

table {
    width: 100%;
}

td {
    background-color: #f7f7e2;
    border: 1px solid rgba(0, 0, 0, .1);
    text-align: center;
}

header {
    position: relative;
}

header button {
    font-weight: bold;
    font-size: 1em;
    width: 2em;
    height: 100%;
    margin: 3px;
    position: absolute;
}

header button:last-of-type {
    top: 0;
    right: 0;
}

.good {
    color: #006528;
}

.day {
    color: #999;
}



